._wPaint_menu {
  padding: 1px;
  background: #EFEFEF;
  border: solid #000000 2px;
  border-radius: 5px;
  box-shadow: 3px 3px 5px #555555;
  z-index: 999999;
}

._wPaint_options {
  float: left;
  padding: 2px;
}

._wPaint_icon,
._wPaint_dropDown,
._wPaint_color {
  float: left;
  height: 18px;
  margin: 0 2px;
  line-height: 16px;
  background-position: center center;
  background-repeat: no-repeat;
  cursor: pointer;
  border-style: solid;
  border-width: 1px;
  padding: 3px;
  border-radius: 5px;
}

._wPaint_icon,
._wPaint_color {
  width: 18px;
}

._wPaint_icon,
._wPaint_dropDown,
._wColorPicker_buttonBorder {
  border-color: #999999
}

._wPaint_icon:hover,
._wPaint_icon.active {
  border-color: #F88017;
  background-color: #E5E5E5;
}

._wPaint_dropDown select {
  margin-top: -2px;
  background-color: #EFEFEF;
  border: none;
  cursor: pointer;
}

._wPaint_handle {
  float: left;
  width: 10px;
  height: 30px;
  background: #AFAFAF;
  border-radius: 3px 0 0 3px;
  cursor: pointer;
}

._wPaint_colorPicker {
  float: left;
  height: 24px;
}

._wPaint_textInput {
  position: absolute;
  border: dotted #0000FF 1px;
  background: none;
  margin: 0px;
  padding: 0px;
  outline-width: 0;
  word-wrap: break-word;
  overflow: hidden;
}

